.detail-container header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0.53rem;
  margin-bottom: 0;
  padding-top: 0.8rem;
}

.detail-container header a img {
  width: 0.64rem;
}

.detail-container header > img {
  width: 2.67rem;
}

.detail-container .location {
  width: 2.67rem;
  margin-left: 0.53rem;
  font-weight: 700;
  font-size: 0.8rem;
}

.detail-container .temp {
  position: relative;
  width: 2.67rem;
  margin-left: 0.32rem;
  font-weight: 700;
  font-size: 2.56rem;
  line-height: 3.07rem;
}

.detail-container .weather-index-list {
  display: flex;
  justify-content: space-between;
  padding: 0 0.53rem;
}

.detail-container .weather-index {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-width: 2.08rem;
  height: 0.91rem;
  padding: 0 0.27rem;
  border-radius: 0.53rem;
  text-align: center;
  font-weight: 700;
  font-size: 0.37rem;
}

.detail-container .weather-index.precip {
  background-color: rgba(101, 142, 217, 0.1);
  color: #658ED9;
}

.detail-container .weather-index.humidity {
  background-color: rgba(216, 97, 145, 0.1);
  color: #D86191;
}

.detail-container .weather-index.wind-speed {
  background-color: rgba(94, 79, 193, 0.1);
  color: #5E4FC1;
}

.detail-container .weather-index img {
  width: 0.64rem;
}

.detail-container .chart-title {
  margin: 0.43rem 0;
  padding-left: 0.53rem;
  text-align: left;
  font-weight: 700;
  font-size: 0.48rem;
  line-height: 0.59rem;
}

.detail-container .temp::after {
  content: '째C';
  position: absolute;
  top: 1.07rem;
  right: -0.32rem;
  font-weight: 500;
  font-size: 0.64rem;
  line-height: 0.45rem;
  color: #332821;
}

.temp-chart-container {
  width: 100%;
  text-align: center;
  /* overflow-x: scroll; */
}

#temp-chart {
  margin: 0 auto;
  /* display: inline-block; */
  width: 100%;
  height: 2.35rem;
}

.detail-container .hour-list {
  display: flex;
  align-items: center;
  overflow-x: scroll;
}

.detail-container .hour-list::-webkit-scrollbar {
  display: none;
}

.detail-container .hour-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 2.4rem;
  padding: 0.8rem 1.6rem;
}

.detail-container .hour-block .temp {
  position: relative;
  width: fit-content;
  margin-left: 0;
  font-weight: 700;
  font-size: 0.96rem;
  line-height: 1.17rem;
}

.detail-container .hour-block .temp::after {
  content: '째C';
  position: absolute;
  top: 0.27rem;
  right: -0.32rem;
  font-weight: 500;
  font-size: 0.37rem;
  color: #332821;
}

.detail-container .hour-block .time {
  width: fit-content;
  font-weight: 500;
  font-size: 0.37rem;
  line-height: 0.45rem;
  text-align: center;
  color: rgba(51, 40, 33, 0.5);
}

.detail-container .week-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 0.53rem;
  padding-left: 0.53rem;
  padding-right: 0.96rem;
}

.detail-container .week-block img {
  width: 1.33rem;
}

.detail-container .week-block .max-temp {
  position: relative;
  width: fit-content;
  font-weight: 700;
  font-size: 0.48rem;
}

.detail-container .week-block .min-temp {
  position: relative;
  width: fit-content;
  margin-left: 0.64rem;
  font-weight: 700;
  font-size: 0.48rem;
  color: rgba(51, 40, 33, 0.5);
}

.detail-container .week-block .temp-list span::after {
  content: '째C';
  position: absolute;
  top: 0.11rem;
  right: -0.21rem;
  font-weight: 500;
  font-size: 0.27rem;
  color: #332821;
}

.detail-container .week-block .temp-list .min-temp::after {
  color: rgba(51, 40, 33, 0.5);
}

.detail-container .week-block .time {
  width: fit-content;
  font-weight: 700;
  font-size: 0.48rem;
  text-align: center;
  color: #332821;
}